
body{
    margin: 0;
    background-color: whitesmoke;
}

p {
    text-align: left;
    overflow: auto;
    padding-right: 2%;
    padding-left:2%;
}

a {
    text-decoration: none;
    font-weight: bold;
}

/*
    bootstrap
*/

.text-right{
    font-size: 0.8em;
}

.card{
    margin: 1%;
    border-radius: 0;
    border-color: rgb(255, 255, 255);
    min-width: auto;
    overflow: auto;
}

/*
    p√°gina
*/

.alinha{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.line{
    width: 80%;
}

.foot{
    background-color: black;
    color: white;
    text-align: center;
    font-family: monospace;
    padding:10px;
    margin: 2% 0 0 0;
    font-size: 0.8em;
}

.mainpage {
    font-family: 'Courier New', Courier, monospace;
    margin: 0 5% 0 5%;
    text-align: center;
    font-size: 1.2em;
}

#myname{
    text-align: center;
    margin-top: 2%;
    font-size:4em;
    font-weight: bold;
    color: #2b2b2b;
}

.topic{
    padding: 2%;
}

/*
    span
*/

.letter{
    font-size: 2em;
    font-weight: bold;
    padding: 10px;
    background-color: black;
    color: white;
}

.focus{
    background-color: yellow;
    color: black;
}

/*
    logos
*/

.logosgroup {
    display: block;
    text-align: center;
  }

.logos {
    cursor:pointer;
    padding:2%;
    font-size: 2em;
    color: #5a5a5a;
}

.logos:hover{
    color: rgb(16, 16, 95);
}
